import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ModalController } from 'ionic-angular';
import { UsersProvider } from '../../providers/users/users';
import { AddressesProvider } from '../../providers/addresses/addresses';
import { OrdersProvider } from '../../providers/orders/orders';
import { CartsProvider } from '../../providers/carts/carts';

import { PayPal, PayPalPayment, PayPalConfiguration,  PayPalPaymentDetails} from '@ionic-native/paypal/ngx';

@IonicPage()
@Component({
  selector: 'page-checkout',
  templateUrl: 'checkout.html',
})
export class CheckoutPage {
  paymentdata: any;
  paymentdetails: any;
  checkoutList: any[] = [];
  payment: any = "paypal";
  delivery: any;
  subTotalAmnt: number = 0.00;
  shippingAmnt: number = 1.00;
  totalAmnt: number = 0.00;
  shippingAddress: any = { id: null };
  billingAddress: any = { id: null };
  user: any = this.usersProvider.currentUser();
  deliveryOption;

  constructor(public payPal: PayPal, public navCtrl: NavController, public cartsProvider: CartsProvider, public ordersProvider: OrdersProvider, public navParams: NavParams, public addressesProvider: AddressesProvider, public usersProvider: UsersProvider, public viewCtrl: ViewController, public modalCtrl: ModalController) {

    this.deliveryOption = [{
      id: 1,
      name: "Standard",
      price: 1.00,
    }, {
      id: 2,
      name: "Express",
      price: 3.00,
    }];

    this.delivery = this.deliveryOption[0]
    this.payment = "paypal";
    this.shippingAmnt = 1;
    this.checkoutList = this.navParams.get('items');
    this.subTotalAmnt = this.navParams.get('subTotal');
    this.totalAmnt = this.subTotalAmnt + this.shippingAmnt;
    this.setAddresses();
  }

  pay(){
    this.payPal.init({
      PayPalEnvironmentProduction: 'YOUR_PRODUCTION_CLIENT_ID',
      PayPalEnvironmentSandbox: 'AbeK5EFgfZVQ3fZRzjOPGURRTN9vkYvuNh_5S3kpJVUAl1BzIiym-lYmJGaCTwgFmlvEil6DRK3sa91d'
    }).then(() => {
      // Environments: PayPalEnvironmentNoNetwork, PayPalEnvironmentSandbox, PayPalEnvironmentProduction
      this.payPal.prepareToRender('PayPalEnvironmentSandbox', new PayPalConfiguration({
        // Only needed if you get an "Internal Service Error" after PayPal login!
        //payPalShippingAddressOption: 2 // PayPalShippingAddressOptionPayPal
      })).then(() => {
        let payment = new PayPalPayment('3.33', 'USD', 'Description', 'sale');
        this.payPal.renderSinglePaymentUI(payment).then(() => {
          // Successfully paid
    
          // Example sandbox response
          //
          // {
          //   "client": {
          //     "environment": "sandbox",
          //     "product_name": "PayPal iOS SDK",
          //     "paypal_sdk_version": "2.16.0",
          //     "platform": "iOS"
          //   },
          //   "response_type": "payment",
          //   "response": {
          //     "id": "PAY-1AB23456CD789012EF34GHIJ",
          //     "state": "approved",
          //     "create_time": "2016-10-03T13:33:33Z",
          //     "intent": "sale"
          //   }
          // }
        }, () => {
          // Error or render dialog closed without being successful
        });
      }, () => {
        // Error in configuration
      });
    }, () => {
      // Error in initialization, maybe PayPal isn't supported or something else
    });
  }


  calculateTotal() {
    this.totalAmnt = +this.subTotalAmnt + +this.delivery.price;
  }

  closeModal() {
    this.viewCtrl.dismiss();
  }

  setAddresses() {
    let billingId = this.user.defaultBillingAddress;
    let shippingId = this.user.defaultShippingAddress;
    if (billingId != null) {
      this.addressesProvider.getAddress(billingId).then(data => {
        this.billingAddress = data;
      });
    }
    else {
      this.billingAddress.id = null;
    }

    if (shippingId != null) {
      this.addressesProvider.getAddress(shippingId).then(data => {
        this.shippingAddress = data;
      });
    }
    else
      this.shippingAddress.id = null;
  }

  changeShippingAddress() {
    let addressId = null;
    if (this.shippingAddress.id != null)
      addressId = this.shippingAddress.id
    var modalPage = this.modalCtrl.create('AddressPickerPage', {
      id: addressId
    });

    modalPage.onDidDismiss((data) => {
      this.addressesProvider.getAddress(data).then(data => {
        this.shippingAddress = data;
      });
    })

    modalPage.present();
  }

  changeBillingAddress() {
    let addressId = null;
    if (this.billingAddress.id != null)
      addressId = this.shippingAddress.id
    var modalPage = this.modalCtrl.create('AddressPickerPage', {
      id: addressId
    });

    modalPage.onDidDismiss((data) => {
      this.addressesProvider.getAddress(data).then(data => {
        this.billingAddress = data;
      });
    })

    modalPage.present();
  }

  create() {
    let products = [];
    this.checkoutList.forEach(item => {
      products.push(item.product);
    });
    let json = {
      "id": null,
      "userId": this.user.id,
      "date": new Date().toString(),
      "status": "Awaiting Payment",
      "productTotal": +this.subTotalAmnt,
      "shippingTotal": +this.shippingAmnt,
      "orderTotal": +this.totalAmnt,
      "delivery": this.delivery,
      "payment": this.payment,
      "products": products,
      "shippingAddress": this.shippingAddress,
      "billingAddress": this.billingAddress
    }

    this.ordersProvider.create(json).then(res => {
      if (res) {
        this.checkoutList.forEach(item => {
          this.cartsProvider.delete(item.id);
        });
        this.navCtrl.popToRoot();
        var modalPage = this.modalCtrl.create('OrderDetailsPage', { data: res });
        modalPage.present();
      }
    })
  }

  ionViewWillEnter() {
    if (this.user.id == 0)
      this.navCtrl.setRoot("LoginPage");
  }

}




